import { Component, OnInit , Input, Output,EventEmitter} from '@angular/core';
import { Patient } from '../model/patient'; 
import { PatientService } from '../service/patient.service'; 
import { MatDialog } from '@angular/material'; 
import { RegisterationFormComponent } from '../registeration-form/registeration-form.component';
import { Department } from '../model/Department';
import { AlertComponent } from '../alert/alert.component'; 
import { Router } from '@angular/router';
import { CommonMessageService } from '../service/common-message.service'; 
import { HelperService } from '../service/helper.service'; 

@Component({
  selector: 'app-patient-profile',
  templateUrl: './patient-profile.component.html',
  styleUrls: ['./patient-profile.component.scss']
})
export class PatientProfileComponent implements OnInit {

  public departments: Department[]; 
  @Input() patient: Patient;
  @Output() action = new EventEmitter(); 
  @Output() onDelete = new EventEmitter(); 

  loading = false;
  $queue; 
  $dequeue; 

  constructor(
    public _patient: PatientService,
    public _editModal:MatDialog, 
    public _confirm:MatDialog, 
    public _message: CommonMessageService, 
    public _helper: HelperService, 
    public _route: Router
  ) { }

  ngOnInit() {
    this._patient.departments().subscribe(
      result => {
        this.departments = result; 
      }
    )
  }
  
  edit(){
    let dialog = this._editModal.open(RegisterationFormComponent,{
      width: '600px', 
      disableClose: true, 
      data: {
        departments: this.departments,
        patient: this.patient,
      }, 
    });
  }

  delete(){
    let confirm = this._confirm.open(AlertComponent, {
      disableClose: true, 
      data: {
        dialog: 'confirm', 
        message: 'Do you realy want to delete this patient?', 
        data: this.patient.id
      }
    }); 

    confirm.afterClosed().subscribe(
      (responce) => {
        if(responce.responce){
          this._patient.deletePatint(responce.data)
          .subscribe(
            (responce) => {
                this._message.httpSuccess('Removed patient');
                this.onDelete.emit(responce); 
            }); 
        }
      }
    )
  }
  queue(id: number){
    this.loading = true; 
    this._patient.queuePatient(id)
    .subscribe(
      (responce) => {
        this.loading = false; 
        this.patient.is_queued = true; 
        this.$queue = responce; 
        this.action.emit({type: 'queue',data: responce}); 
      }, 
      (error) => {
        this.loading = false; 
        console.log(error);
      } 
    ); 
  }
  removeQueue(id: number){
    this.loading = true; 
    this._patient.removePatientQueue(id)
    .subscribe(
      (responce) => {
        this.loading = false; 
        this.patient.is_queued = false;
        this.$dequeue = this.patient; 
        this.action.emit({type: 'dequeue',data: this.patient});  
      }, 
      (error) => {
        this.loading = false; 
        console.log(error);
      } 
    ); 
  }


}
