import { Component, OnInit, Input } from '@angular/core';
import { PatientQueue } from '../model/PatientQueue'; 
import { PatientService } from '../service/patient.service'; 
import { Patient } from '../model/patient';
import { Router } from '@angular/router'; 
import * as momnet from 'moment'; 

@Component({
  selector: 'app-queued-patients-list',
  templateUrl: './queued-patients-list.component.html',
  styleUrls: ['./queued-patients-list.component.scss']
})
export class QueuedPatientsListComponent implements OnInit {

  patients: PatientQueue[]; 
  hasQueue = false; 

  constructor(public _patient: PatientService, public _route: Router) { 
    this.patients = []; 
  }
  
  @Input() 
   set push(value: PatientQueue){
    if(value){
        console.log(this.patients);
        this.patients.unshift(value); 
        this.hasQueue = true; 
    }
  }

  @Input()
  set remove(value: Patient){
    if(value){
      this.patients = this.patients.filter(function(val){
          return value.reg_id != val.patient.reg_id; 
      }); 
      this.hasQueue = (this.patients.length)?true:false;;
    }
  }
  
  ngOnInit() {
    this._patient.patientsInQueue()
    .subscribe(
      (responce) => {
        if(responce.length > 0){
          responce.forEach(patient => {
            patient.humanWaitingTime = momnet(patient.created_at).fromNow(); 
          });
          this.patients = responce; 
          this.hasQueue = true; 
        }else{ this.hasQueue = false; }
      }
    )
  }

  view(reg_id){
    this._route.navigate(['/patient/'+reg_id])
  }

  openQueue(){
    console.log('start'); 
    this._route.navigate(['patinet/queue']); 
  }
}
