import { Routes } from '@angular/router'; 
import { DashboardComponent } from './dashboard/dashboard.component';
import { UsersComponent } from './users/users.component'; 
import { UserProfileRouteComponent } from './user-profile-route/user-profile-route.component';
import { SettingsComponent } from './settings/settings.component';
import { ChangePasswordRouteComponent } from './change-password-route/change-password-route.component';
import { UploadProfilePicRouteComponent } from './upload-profile-pic-route/upload-profile-pic-route.component'; 
import { QueueComponent } from './queue/queue.component'; 
import { FullRecentVisitComponent } from './full-recent-visit/full-recent-visit.component'; 
export const appRoutes: Routes = [
    // {path: '', redirectTo: 'home', pathMatch: 'full' }, 
    {path: '', component: DashboardComponent },
    {path: 'users', component: UsersComponent },
    {path: 'settings', component: SettingsComponent }, 
    {path: 'settings/change password', component: ChangePasswordRouteComponent }, 
    {path: 'settings/change profile pic', component: UploadProfilePicRouteComponent }, 
    {path: ':worker_id', component: UserProfileRouteComponent}, 
    {path: 'patient/:patientId', component: DashboardComponent}, 
    {path: 'patinet/visits/:patientId', component: FullRecentVisitComponent}, 
    {path: 'patinet/queue', component: QueueComponent}
]; 