import { Component, OnInit, Output,EventEmitter,Input } from '@angular/core';
import { FormControl } from '@angular/forms';
import { Patient } from '../model/patient';
import { PatientService } from '../service/patient.service';

@Component({
  selector: 'app-search-patient',
  templateUrl: './search-patient.component.html',
  styleUrls: ['./search-patient.component.scss']
})
export class SearchPatientComponent implements OnInit {

  search: FormControl; 
  patients: Patient[];

  @Output() patient = new EventEmitter<Patient>(); 
  @Output() clear = new EventEmitter(); 
  @Input() hideTitel = false; 
  @Input()
  set key(value: string){
    if(value){
      this.onSearch(value);
      this.search.setValue(value); 
    } 
  }
  constructor(public _patient: PatientService) { 
    this.search = new FormControl(); 
  }

  ngOnInit() {
    this.search.valueChanges
    .subscribe((value)=>{
      console.log(value); 
      if(value != ''){
        this._patient.searchAutoComplate(value)
        .subscribe((responce)=>{
          this.patients = responce; 
        }); 
      }else{
        this.clear.emit('noSearchResult'); 
      }
       
    });
  }

  onSearch(key){
    this._patient.search(key)
    .subscribe(
      (patient) => {
        this.patient.emit(patient); 
      },(error) => {
        this.patient.emit(null); 
      }
    )
  }
  onSelect(patient){
    this.patient.emit(patient); 
  }
  onKeydown(e){
    if(e.key == 'Enter'){
      this.onSearch(this.search.value); 
    }
  }
}
