import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http'; 
import { Observable } from 'rxjs';
import { Patient } from '../model/patient';
import { Department } from '../model/Department';
import { College } from '../model/College';
import { HttpResponse } from '../model/HttpResponse'; 
import { PatientQueue } from '../model/PatientQueue'; 
import { queue } from 'rxjs/internal/scheduler/queue';

@Injectable({
  providedIn: 'root'
})
export class PatientService {

  constructor(public _http: HttpClient ) { }
  root = " http://clinic.com"; 

  newPatient(patient: Patient): Observable<Patient>{
    const URL = this.root+"/ajax/post/patient/new"; 
    return this._http.post<Patient>(URL, patient); 
  }

  updatePatient(patient: Patient): Observable<Patient>{
    const URL = this.root+"/ajax/update/patient"; 
    return this._http.put<Patient>(URL, patient); 
  }

  deletePatint(id: number){
    const URL = this.root + "/ajax/delete/patient/"+id; 
    return this._http.delete(URL); 
  }
  departments(): Observable<Department[]>{
    const URL = this.root+"/ajax/get/departments"; 
    return this._http.get<Department[]>(URL); 
  }
  
  deletePatient(patientId: number): Observable<HttpResponse>{
    const URL = this.root+ "/ajax/delete/patient/"+patientId; 
    return this._http.delete<HttpResponse>(URL); 
  }
  
  collages(): Observable<College[]>{
    const URL = this.root+"/ajax/get/collages";
    return this._http.get<College[]>(URL);  
  }

  searchAutoComplate(key: string): Observable<Patient[]>{
    const URL = this.root+"/ajax/get/search/auto complete/"+key;
    return this._http.get<Patient[]>(URL); 
  }

  search(key: string): Observable<Patient>{
    const URL = this.root+"/ajax/get/search/"+key; 
    return this._http.get<Patient>(URL); 
  }

  queuePatient(id: number): Observable<HttpResponse>{
    const URL = this.root+"/ajax/post/queue/add/"+id; 
    return this._http.post<HttpResponse>(URL, {}); 
  }

  dequeuePatient(id: number): Observable<HttpResponse>{
    const URL = this.root+"/ajax/post/queue/remove/"+id; 
    return this._http.post<HttpResponse>(URL, {}); 
  }

  removePatientQueue(patientId: number){
    const URL = this.root+"/ajax/delete/queue/"+patientId; 
    return this._http.delete(URL); 
  }

  recentVisits(patientId: number): Observable<PatientQueue[]>{
    const URL = this.root+ "/ajax/post/queue/recent/"+patientId; 
    return this._http.get<PatientQueue[]>(URL); 
  }

  patientsInQueue() : Observable<PatientQueue[]>{
    const URL = this.root + "/ajax/get/queue/patients in queue"; 
    return this._http.get<PatientQueue[]>(URL); 
  }
}
