import { Component, OnInit, Input } from '@angular/core';
import { PatientService } from '../service/patient.service'; 
import { PatientQueue } from '../model/PatientQueue'; 
import { Router } from '@angular/router'; 
import * as moment from 'moment' ; 

@Component({
  selector: 'app-recent-patient-visit',
  templateUrl: './recent-patient-visit.component.html',
  styleUrls: ['./recent-patient-visit.component.scss']
})
export class RecentPatientVisitComponent implements OnInit {

  visits: PatientQueue[]; 
  hasQueue = true; 
  $patientId: number;
 
  constructor(
    public _patient: PatientService, 
    public _route: Router) { }

  ngOnInit() {
  }
  @Input() 
  set patientId(id: number){
    this.$patientId = id; 
    this._patient.recentVisits(id)
    .subscribe(
      (responce: PatientQueue[])=>{
        
        this.hasQueue = (responce.length > 0)
        this.visits = responce; 
      }, 
      (error) => {
      }
    )
  }; 
 
  @Input()
  set push(value){
    console.log(value); 
    if(value)
      this.visits.unshift(value); 
  }

  @Input()
  set remove(value){
    if(value){
      this.visits = this.visits.filter(function(val){
          return value.reg_id != val.patient.reg_id; 
      }); 
    }
  }
  openVisits(patientId){
    this._route.navigate(['patinet/visits/'+patientId]); 
  }

}
