import { Component, OnInit } from '@angular/core';
import { Patient } from '../model/patient';
import { ActivatedRoute } from '@angular/router';
import { PatientService } from '../service/patient.service'; 

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss']
})
export class DashboardComponent implements OnInit {

  patient: Patient;
  queued:Patient = null;  
  dequeued:Patient = null; 
  search: string = null; 
  mobile: boolean; 
  noSearchResult = false; 

  constructor(public _route: ActivatedRoute,public _patient: PatientService) { }

  ngOnInit() {
    this._route.params.subscribe(
      (param) => {
        if(param.patientId && param.patientId !== 'undefined'){
          this.search = param.patientId; 
        } 
      }
    )
    const that = this; 
    window.addEventListener('resize', function(e){
      that.mobile = (window.screen.width < 800); 
    }); 
    this.mobile = (window.screen.width < 800); 
  }
  onSearchResponce(patient){
    if(patient){
      this.patient = patient;
    }else{
      this.noSearchResult = true; 
      this.patient = null; 
    } 
    
  }
  onAction(e){
    switch(e.type){
      case 'queue': 
        this.queued = e.data; 
      break; 
      case 'dequeue': 
        this.dequeued = e.data; 
        // console.log(e.data); 
      break; 
    }
  }

  onDelete(e){
    this.patient = null; 
    this.search = '';
    this.dequeued = e;    
  }

  clear(){
    this.noSearchResult = false; 
  }
}
