import { Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator, MatSort } from '@angular/material';
import { FullRecentVisitDataSource } from './full-recent-visit-datasource';
import { PatientService } from '../service/patient.service'; 
import { ActivatedRoute } from '@angular/router'; 
import { PatientQueue } from  '../model/PatientQueue'; 
@Component({
  selector: 'app-full-recent-visit',
  templateUrl: './full-recent-visit.component.html',
  styleUrls: ['./full-recent-visit.component.css']
})
export class FullRecentVisitComponent implements OnInit {
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  dataSource: FullRecentVisitDataSource;
  data: PatientQueue[];

  constructor(public _patient: PatientService, public _route: ActivatedRoute){}
  displayedColumns = ['date', 'name'];

  ngOnInit() {
    this.dataSource = new FullRecentVisitDataSource(this.paginator, this.sort, []);
    this._route.params.subscribe((param) =>{
        this._patient.recentVisits(param.patientId)
            .subscribe(
              (visits) => {
                this.data= visits; 
                this.paginator.length = visits.length; 
                this.dataSource = new FullRecentVisitDataSource(this.paginator, this.sort, visits);
              }
            )
    }); 
    
  }
}
