import { DataSource } from '@angular/cdk/collections';
import { MatPaginator, MatSort } from '@angular/material';
import { map } from 'rxjs/operators';
import { Observable, of as observableOf, merge } from 'rxjs';
import { PatientQueue } from "../model/PatientQueue"; 

export class FullRecentVisitDataSource extends DataSource<PatientQueue> {
  data: PatientQueue[];

  constructor(private paginator: MatPaginator, private sort: MatSort, public _pateient: PatientQueue[]) {
    super();
    this.data = _pateient; 
  }

  connect(): Observable<PatientQueue[]> {
   
    const dataMutations = [
      observableOf(this.data),
      this.paginator.page,
      this.sort.sortChange
    ];

    this.paginator.length = this.data.length;

    return merge(...dataMutations).pipe(map(() => {
      return this.getPagedData(this.getSortedData([...this.data]));
    }));
  }

  disconnect() {}

  
  private getPagedData(data: PatientQueue[]) {
    const startIndex = this.paginator.pageIndex * this.paginator.pageSize;
    return data.splice(startIndex, this.paginator.pageSize);
  }


  private getSortedData(data: PatientQueue[]) {
    if (!this.sort.active || this.sort.direction === '') {
      return data;
    }

    return data.sort((a, b) => {
      const isAsc = this.sort.direction === 'asc';
      switch (this.sort.active) {
        case 'date': return compare(a.created_at, b.created_at, isAsc);
        case 'name': return compare(a.physician.first_name+" "+a.physician.father_name, b.physician.first_name+" "+b.physician.father_name, isAsc);
        default: return 0;
      }
    });
  }
}

function compare(a, b, isAsc) {
  return (a < b ? -1 : 1) * (isAsc ? 1 : -1);
}
