import { Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator, MatSort } from '@angular/material';
import { QueueDataSource } from './queue-datasource';
import { PatientService } from '../service/patient.service'; 
import * as monment from 'moment'; 
import { PatientQueue } from '../model/PatientQueue'; 

@Component({
  selector: 'app-queue',
  templateUrl: './queue.component.html',
  styleUrls: ['./queue.component.css']
})
export class QueueComponent implements OnInit {
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  dataSource: QueueDataSource;
  data: PatientQueue[] = [];
  
  /** Columns displayed in the table. Columns IDs can be added, removed, or reordered. */
  displayedColumns = ['queue_number', 'name', 'waiting_time'];

  constructor(public _patinet: PatientService){}
  ngOnInit() {
    this.dataSource = new QueueDataSource(this.paginator, this.sort, this.data); 
    this._patinet.patientsInQueue()
    .subscribe(
      (patientQueues) => {     
        this.dataSource = new QueueDataSource(this.paginator, this.sort,patientQueues ); 
      }
    )
  }
}
