import { DataSource } from '@angular/cdk/collections';
import { MatPaginator, MatSort } from '@angular/material';
import { map } from 'rxjs/operators';
import { Observable, of as observableOf, merge } from 'rxjs';
import { PatientQueue } from "../model/PatientQueue"; 
import { Patient } from '../model/patient'; 
import { PatientService } from '../service/patient.service'; 

export class QueueDataSource extends DataSource<PatientQueue> {

  constructor(private paginator: MatPaginator, private sort: MatSort, public data: PatientQueue[]) {
    super();
  }
  
  connect(): Observable<PatientQueue[]> {
    const dataMutations = [
      observableOf(this.data),
      this.paginator.page,
      this.sort.sortChange
    ];

    return merge(...dataMutations).pipe(map(() => {
      return this.getPagedData(this.getSortedData());
    }));
  }

  disconnect() {}

  private getPagedData(data: PatientQueue[]) {
    const startIndex = this.paginator.pageIndex * this.paginator.pageSize;
    console.log(this.data); 
    return data.splice(startIndex, this.paginator.pageSize);
  }

  private getSortedData() {
    if (!this.sort.active || this.sort.direction === '') {
      return this.data;
    }

    return this.data.sort((a, b) => {
      const isAsc = this.sort.direction === 'asc';
      switch (this.sort.active) {
        case 'name': return compare(a.patient.first_name + ' ' + a.patient.father_name,
                                    b.patient.first_name+ ' ' + a.patient.father_name, isAsc);
        case 'queue_number': return compare(+a.queue_number, +b.queue_number, isAsc);
        case 'waiting_time': return compare(a.created_at,b.created_at, isAsc); 
        default: return 0;
      }
    });
  }
}

/** Simple sort comparator for example ID/Name columns (for client-side sorting). */
function compare(a, b, isAsc) {
  return (a < b ? -1 : 1) * (isAsc ? 1 : -1);
}
